.slider {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image {
    width: 1000px;
    height: 600px;
    border-radius: 10px;
}

.right-arrow {
    position: absolute;
    height: 100px;
        width: 100px;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: rgba(12, 10, 65, 0.863);
    z-index: 10;
    cursor: pointer;
    user-select: none;
}

.left-arrow {
    position: absolute;
    height: 100px;
    width: 100px;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: rgba(12, 10, 65, 0.863);
    z-index: 10;
    cursor: pointer;
    user-select: none;
}